import React from 'react';
import { Container, Row, Col } from 'reactstrap';
import './Portfolio.css';
import img01 from '../../assests/proyecto1.png';
import img02 from '../../assests/proyecto2.png';
import img03 from '../../assests/proyecto3.png';
import img04 from '../../assests/proyecto4.png';

const portfolioData =[
  {
    imgUrl: img02,
    title: 'E-commerce Chilly',
    url: 'https://chilly-production.up.railway.app/',
    desc: 'Plataforma en lnea para la venta y armado de PC con caractersticas como UI amigable, catlogo de productos, carrito de compras, armado personalizado, pasarela de pago, chatbot y dashboard para administrador y usuarios registrados.',
  },
  {
    imgUrl: img01,
    title: 'SPA Pokmon',
    url: 'https://pi-pokemon.netlify.app/',
    desc: 'Aplicacin de Pokmon con pgina de inicio con una lista de 150 cards de personajes con paginacin, bsqueda, filtros, ordenamiento, seccin de detalles, y seccin de creacin con formulario controlado y validaciones. Modelo responsive.',
  },
  {
    imgUrl: img03,
    title: 'Portafolio',
    url: 'https://cinthiapiera.github.io/Portafolio/',
    desc: 'Sitio web personal que funciona como portafolio para mostrar habilidades y experiencia. Cuenta con una estructura clara y bien organizada, con secciones que ofrecen informacin detallada, proyectos destacados y datos de contacto. Modelo responsive.',
  },
  {
    imgUrl: img04,
    title: 'Encriptador de texto',
    url: 'https://cinthiapiera.github.io/encriptador/',
    desc: 'Aplicacin que permite encriptar y desencriptar mensajes para proteger la privacidad de la informacin. Adems, la interfaz es clara e intuitiva sin la necesidad de conocimientos tcnicos avanzados. Modelo responsive. ',
  }
]

const Portfolio = () => {
  return (
    <section id="proyectos">
      <Container>
        <Row>
          <Col lg='12' className='services__top mb-5'>
            <h6>Explora mi  trabajo y dame tu feedback</h6>
            <h2>Proyectos</h2>
          </Col>
          {
              portfolioData.map((item,index)=>(
                <Col lg='4' md='6' sm='6' key={index} className='mb-4'>
                  <div className="single__service">
                    <a href={item.url} target="_blank" rel="noopener noreferrer">
                      <img src={item.imgUrl} alt={item.title} className='w-100'/>
                    </a>
                    <h2>{item.title}</h2>
                    <p>{item.desc}</p>
                  </div>
              </Col>
              ))
            }
          
        </Row>
      </Container>
    </section>
  )
}

export default Portfolio;